<template>
  <div class="detail-container">
    <div class="search">
      <div class="concatField">
        <el-input
          @keyup.native="_search()"
          placeholder="请输入关键字"
          v-model="concatField"
          clearable>
        </el-input>
        <el-button type="primary" @click="_search()">搜索</el-button>
        <el-button type="warning" @click="isShow = !isShow"> 精确搜索</el-button>
        <ul class="list_menu">
          <li v-show="superShow == 'true'" @click="insertShow = true">新增</li>
          <li v-show="superShow == 'true'" @click="updateShow = true">更新</li>
          <li @click="softDelete()">删除</li>
          <li ><a @click="exportIP($event)" href="javascript:void(0)">导出</a></li>
          <li>
            <span>
              导入
              <input type="file" id="file"  @change="importFile($event)"  class="inputstyle"  title="请选择.excel">
            </span>
          </li>
        </ul>
      </div>
      <transition name="slide">
        <div v-if="isShow" class="detail_father">
          <div class="detail">
            <div class="detail-item">
              <span class="input-label">虚拟id：</span>
              <el-input class="detail-input" @keyup.native="_search()"  v-model="virtualId"></el-input>
            </div>
            <div class="detail-item">
              <span class="input-label">id：</span>
              <el-input class="detail-input" @keyup.native="_search()"  v-model="id"></el-input>
            </div>
            <div class="detail-item">
              <span class="input-label">文号：</span>
              <el-input class="detail-input" @keyup.native="_search()"  v-model="fileId"></el-input>
            </div>
            <div class="detail-item">
              <span class="input-label">题名：</span>
              <el-input class="detail-input" @keyup.native="_search()"  v-model="title"></el-input>
            </div>
          </div>
          <div class="detail">
            <div class="detail-item">
              <span class="input-label">责任人：</span>
              <el-input class="detail-input" @keyup.native="_search()"  v-model="responsePerson"></el-input>
            </div>
            <div class="detail-item">
              <span class="input-label">备注：</span>
              <el-input class="detail-input"  @keyup.native="_search()" v-model="note"></el-input>
            </div>
            <div class="detail-item">
              <span class="input-label">秘级：</span>
              <el-select v-model="level" placeholder="请选择" @change="_search()" class="detail-input">
                <el-option v-for="item in levelOptions" :key="item.value"
                           :value="item.value"
                           :label="item.label">
                </el-option>
              </el-select>
            </div>
            <div class="detail-item">
              <span class="input-label">文件类型：</span>
              <el-select v-model="fileType" @change="_search()" placeholder="请选择" class="detail-input">
                <el-option v-for="item in fileTypeOptions" :key="item.value"
                           :value="item.value"
                           :label="item.label">
                </el-option>
              </el-select>
            </div>
          </div>
          <div class="detail">
            <div class="block">
              <span class="input-label">起始时间：</span>
              <el-date-picker
                @change="_search()"
                class="detail-input"
                value-format="yyyy-MM-dd"
                v-model="time"
                type="daterange"
                align="right"
                unlink-panels
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
              >
              </el-date-picker>
            </div>
          </div>
        </div>
      </transition>
    </div>
    <div class="table">
      <el-table
        @selection-change="handleSelectionChange"
        :data="tableData"
        style="width: 100%">
        <el-table-column
          type="selection"
          width="55">
        </el-table-column>
        <el-table-column
          prop="virtualId"
          label="序号"
          >
        </el-table-column>
        <el-table-column
          prop="id"
          label="档号"
          >
        </el-table-column>
        <el-table-column
          prop="rollId"
          label="卷号">
        </el-table-column>
        <el-table-column
          prop="fileId"
          label="文号">
        </el-table-column>
        <el-table-column
          prop="responsePerson"
          label="责任者">
        </el-table-column>
        <el-table-column
          prop="title"
          label="题名">
          <template slot-scope="scope">
            <span v-if="scope.row.fileColor == 'red'" style="color: #F56C6C">{{ scope.row.title }}</span>
            <span v-else-if="scope.row.fileColor == 'blue'" style="color: #409EFF">{{ scope.row.title }}</span>
            <span v-else-if="scope.row.fileColor == 'green'" style="color: #67C23A">{{ scope.row.title }}</span>
            <span v-else-if="scope.row.fileColor == 'yellow'" style="color: #E6A23C">{{ scope.row.title }}</span>
            <span v-else>{{ scope.row.title }}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="level"
          label="密级">
        </el-table-column>
        <el-table-column
          prop="pageCount"
          label="页数">
        </el-table-column>
        <el-table-column
          prop="secretDate"
          label="保密年限(年)"
          width="100">
        </el-table-column>
        <el-table-column
          prop="date"
          label="日期"
          width="180">
        </el-table-column>
        <el-table-column
          prop="note"
          label="备注"
          width="180">
          <template slot-scope="scope">
            <el-input @keyup.enter.native.prevent="changeNote(scope.row.virtualId, scope.row.id)" v-model="scope.row.note" placeholder="请输入内容"></el-input>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="page">
      <el-pagination
        background
        layout="prev, pager, next"
        :total="pageCount"
      @current-change="changePage">
      </el-pagination>
    </div>
    <div class="dialog">
      <el-dialog :visible.sync="insertShow" @open="openDialog" title="新增">
        <div class="detail_father">
          <div class="detail">
            <div class="detail-item">
              <span class="input-label">id：</span>
              <el-input class="detail-input" v-model="dialogData.id"></el-input>
            </div>
            <div class="detail-item">
              <span class="input-label">文号：</span>
              <el-input class="detail-input" v-model="dialogData.fileId"></el-input>
            </div>
            <div class="detail-item">
              <span class="input-label">卷号：</span>
              <el-input class="detail-input"  v-model="dialogData.rollId"></el-input>
            </div>
          </div>
          <div class="detail">
            <div class="detail-item">
              <span class="input-label">责任人：</span>
              <el-input class="detail-input" v-model="dialogData.responsePerson"></el-input>
            </div>
            <div class="detail-item">
              <span class="input-label">题名：</span>
              <el-input class="detail-input" v-model="dialogData.title"></el-input>
            </div>
            <div class="detail-item">
              <span class="input-label">页数：</span>
              <el-input class="detail-input"  v-model="dialogData.pageCount"></el-input>
            </div>
          </div>
          <div class="detail">
            <div class="detail-item">
              <span class="input-label">日期：</span>
              <el-date-picker
                type="date"
                placeholder="选择日期"
                v-model="dialogData.date">
              </el-date-picker>
            </div>
            <div class="detail-item">
              <span class="input-label">保密年限：</span>
              <el-input class="detail-input" type="number" v-model="dialogData.secretDate"></el-input>
            </div>
            <div class="detail-item">
              <span class="input-label">备注：</span>
              <el-input class="detail-input"  v-model="dialogData.note"></el-input>
            </div>
          </div>
          <div class="detail">
            <div class="detail-item">
              <span class="input-label" >文件类型：</span>
              <el-select  placeholder="请选择" class="detail-input" v-model="dialogData.fileType" style="width: 100%">
                <el-option v-for="item in fileTypeOptions" :key="item.value"
                           :value="item.value"
                           :label="item.label">
                </el-option>
              </el-select>
            </div>
            <div class="detail-item">
              <span class="input-label">密级：</span>
              <el-select placeholder="请选择" class="detail-input" v-model="dialogData.level" style="width: 100%">
                <el-option v-for="item in levelOptions" :key="item.value"
                           :value="item.value"
                           :label="item.label">
                </el-option>
              </el-select>
            </div>
          </div>
        </div>
        <div slot="footer" class="dialog-footer">
          <el-button @click="insertShow = false">取 消</el-button>
          <el-button type="primary" @click="_insertClick">确 定</el-button>
        </div>
      </el-dialog>
    </div>
    <div class="dialog">
      <el-dialog :visible.sync="updateShow" @open="openDialog" title="更新">
        <div class="detail_father">
          <div class="detail">
            <div class="detail-item">
              <span class="input-label">id：</span>
              <el-input class="detail-input" v-model="dialogData.id"></el-input>
            </div>
            <div class="detail-item">
              <span class="input-label">文号：</span>
              <el-input class="detail-input" v-model="dialogData.fileId"></el-input>
            </div>
            <div class="detail-item">
              <span class="input-label">卷号：</span>
              <el-input class="detail-input"  v-model="dialogData.rollId"></el-input>
            </div>
          </div>
          <div class="detail">
            <div class="detail-item">
              <span class="input-label">责任人：</span>
              <el-input class="detail-input" v-model="dialogData.responsePerson"></el-input>
            </div>
            <div class="detail-item">
              <span class="input-label">题名：</span>
              <el-input class="detail-input" v-model="dialogData.title"></el-input>
            </div>
            <div class="detail-item">
              <span class="input-label">页数：</span>
              <el-input class="detail-input"  v-model="dialogData.pageCount"></el-input>
            </div>
          </div>
          <div class="detail">
            <div class="detail-item">
              <span class="input-label">日期：</span>
              <el-date-picker
                type="date"
                placeholder="选择日期"
                v-model="dialogData.date">
              </el-date-picker>
            </div>
            <div class="detail-item">
              <span class="input-label">保密年限：</span>
              <el-input class="detail-input" type="number"  v-model="dialogData.secretDate"></el-input>
            </div>
            <div class="detail-item">
              <span class="input-label">备注：</span>
              <el-input class="detail-input"  v-model="dialogData.note"></el-input>
            </div>
          </div>
          <div class="detail">
            <div class="detail-item">
              <span class="input-label" >文件类型：</span>
              <el-select  placeholder="请选择" class="detail-input" v-model="dialogData.fileType" style="width: 100%">
                <el-option v-for="item in fileTypeOptions" :key="item.value"
                           :value="item.value"
                           :label="item.label">
                </el-option>
              </el-select>
            </div>
            <div class="detail-item">
              <span class="input-label">密级：</span>
              <el-select placeholder="请选择" class="detail-input" v-model="dialogData.level" style="width: 100%">
                <el-option v-for="item in levelOptions" :key="item.value"
                           :value="item.value"
                           :label="item.label">
                </el-option>
              </el-select>
            </div>
          </div>
        </div>
        <div slot="footer" class="dialog-footer">
          <el-button @click="updateShow = false">取 消</el-button>
          <el-button type="primary" @click="_updateClick">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
  import {search, exportString, importExcel, insertClick, updateClick} from 'api/fileDetail'
  import * as config from 'api/config'
  export default {
    name: 'fileDetail',
    mounted () {
      this._search()
    },
    methods: {
      _search () {
        let data = {
          page: this.page,
          concatField: this.concatField
          // fileId: this.fileId,
          // virtualId: this.virtualId,
          // id: this.id,
          // responsePerson: this.responsePerson,
          // start: this.time[0],
          // end: this.time[1],
          // title: this.title,
          // level: this.level,
          // note: this.note,
          // fileType: this.fileType
        }
        search(data).then(res => {
          // console.log(res)
          if (res.data.status === config.ERR_OK) {
            this.tableData = res.data.data
            this.pageCount = res.data.pageCount
          } else {
            this.tableData = []
            this.pageCount = 1
          }
        })
      },
      changePage (val) {
        this.page = val
        this._search()
      },
      exportIP (event) {
        if (!this.time) {
          this.time = ['', '']
        }
        let str = exportString + `?id = ${this.id}
        + &fileId = ${this.fileId}
        + &responsePerson = ${encodeURIComponent(this.responsePerson)}
        + &title = ${encodeURIComponent(this.title)}
        + &level = ${this.level}
        + &start = ${this.time[0]}
        + &end = ${this.time[1]}
        + &note = ${encodeURIComponent(this.note)}
        + &fileType = ${this.fileType}
        + &concatField = ${encodeURIComponent(this.concatField)}`
        event.target.href = str
      },
      importFile (event) {
        let fd = new FormData()
        fd.append('file', event.target.files[0])
        fd.append('fileType', this.fileType)
        let config = {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        }
        importExcel(fd, config).then(res => {
          if (res.data.status === config.ERR_OK) {
            this.page = 1
            this._search() // 刷新数据
            this.insertShow = false // 关闭弹窗
            this.$msgbox({
              type: 'info',
              message: res.data.msg
            })
          } else {
            this.$alert(res.data.msg, '提示')
          }
        })
        document.getElementById('file').value = ''
      },
      softDelete () {},
      handleSelectionChange (val) {
        this.multipleSelection = val
      },
      _insertClick () {
        let data = {
          id: this.dialogData.id,
          fileId: this.dialogData.fileId,
          rollId: this.dialogData.rollId,
          responsePerson: this.dialogData.responsePerson,
          title: this.dialogData.title,
          level: this.dialogData.level,
          date: this.dialogData.date,
          secretDate: this.dialogData.secretDate,
          note: this.dialogData.note,
          fileType: this.dialogData.fileType,
          pageCount: this.dialogData.pageCount
        }
        insertClick(data).then(res => {
          if (res.data.status === config.ERR_OK) {
            this.page = 1
            this._search() // 刷新数据
            this.$msgbox({
              type: 'info',
              message: res.data.msg
            })
          } else {
            this.$alert(res.data.msg, '提示')
          }
        })
      },
      _updateClick () {
        let data = {
          id: this.dialogData.id,
          fileId: this.dialogData.fileId,
          rollId: this.dialogData.rollId,
          responsePerson: this.dialogData.responsePerson,
          title: this.dialogData.title,
          level: this.dialogData.level,
          date: this.dialogData.date,
          secretDate: this.dialogData.secretDate,
          note: this.dialogData.note,
          fileType: this.dialogData.fileType,
          pageCount: this.dialogData.pageCount
        }
        updateClick(data).then(res => {
          if (res.data.status === config.ERR_OK) {
            this.page = 1
            this._search() // 刷新数据
            this.$msgbox({
              type: 'info',
              message: res.data.msg
            })
          } else {
            this.$alert(res.data.msg, '提示')
          }
        })
      },
      openDialog () {
        this.dialogData = {
          id: '',
          fileId: '',
          rollId: '',
          responsePerson: '',
          title: '',
          level: '',
          date: '',
          secretDate: '',
          note: '',
          fileType: '',
          pageCount: ''
        }
      },
      changeNote (note, virtualId) {
        let data = {
          virtualId: virtualId,
          note: note
        }
        updateClick(data).then(res => {
          if (res.data.status === config.ERR_OK) {
            this._search() // 刷新数据
            this.$msgbox({
              type: 'info',
              message: '备注修改成功'
            })
          } else {
            this.$alert('修改失败', '提示')
          }
        })
      }
    },
    data () {
      return {
        superShow: localStorage.getItem('super') || false,
        insertShow: false,
        updateShow: false,
        dialogData: {
          id: '',
          fileId: '',
          rollId: '',
          responsePerson: '',
          title: '',
          level: '',
          date: '',
          secretDate: '',
          note: '',
          fileType: '',
          pageCount: ''
        },
        file: '',
        isShow: false,
        page: 1,
        pageCount: 1,
        concatField: '',
        fileId: '',
        virtualId: '',
        id: '',
        responsePerson: '',
        title: '',
        level: '',
        time: '',
        note: '',
        fileType: '',
        levelOptions: [
          {
            value: '',
            label: '请选择'
          },
          {
            value: '绝密',
            label: '绝密'
          },
          {
            value: '机密',
            label: '机密'
          },
          {
            value: '秘密',
            label: '秘密'
          },
          {
            value: '一般',
            label: '一般'
          }
        ],
        fileTypeOptions: [
          {
            value: '',
            label: '请选择'
          },
          {
            value: '县级文件',
            label: '县级文件'
          },
          {
            value: '内部传真',
            label: '内部传真'
          },
          {
            value: '机要文件',
            label: '机要文件'
          },
          {
            value: '密码电报',
            label: '密码电报'
          },
          {
            value: '县局文件',
            label: '县局文件'
          }
        ],
        tableData: [],
        multipleSelection: []
      }
    }
  }
</script>

<style scoped lang="stylus">
  .detail_father .detail-item  /deep/ .el-input__inner
    height 30px
    line-height 30px
  .detail_father .detail-item  /deep/ .el-input__icon
    line-height 30px
  .block .el-range-editor
    height 30px
    line-height 30px
  .block .el-date-editor /deep/ .el-range__icon
    line-height 25px
  .block .el-date-editor /deep/ .el-range-separator
    width 7%
    line-height 25px
  .slide-enter-active,.slide-leave-active
    transition: all .3s ease
  .slide-enter, .slide-leave-to
    transform: translateY(-10px)
    opacity: 0
  .detail-container
    .search
      .concatField
        overflow hidden
        .el-input
          width 400px
        .list_menu
          overflow: hidden
          float right
          width 40%
          li
            line-height 40px
            margin-right 10px
            float right
            font-size 14px
            &:last-child
              width 80px
              text-align right
            &:hover
              cursor pointer
              text-decoration underline
            span
              position relative
              display block
              .inputstyle
                position absolute
                width 80px
                top 8px
                right  0
                opacity 0
                cursor inherit
                border none
      .detail_father
        border-top: 1px dashed #ddd
        margin-top: 25px
        .detail
          display flex
          margin-top 20px
          .detail-item
            flex 1
            .el-input
              display inline-block
              width auto
            .input-label
              font-size 14px
              color #666
          .block
            .input-label
              font-size 14px
              color #666
    .table
      margin 30px 0
    .page
      text-align center
      margin 25px 0 50px 0
    .dialog
      .detail
        display flex
        margin 15px 0
        .detail-item
          flex 1
          padding 0 15px
          .el-date-editor.el-input, .el-date-editor
            width auto
</style>



